:root {
    // background
    --color-background-main: #ffffff;
    --color-background-second: #F7F7F7;
    --color-background-accent: #191C26;

    // text
    --color-text-main: #303030;
    --color-text-second: #555555;
    --color-text-white: #ffffff;
    --color-text-light: #9DA4BD;

    // accent
    --color-accent: #FF6C00;

    //shadow
    --color-shadow: #5555554d;

    // fonts
    --font-family-main: 'Open Sans', sans-serif;
    --font-family-title: 'Raleway', sans-serif;

    // transition
    --animation: 250ms cubic-bezier(0.4, 0, 0.2, 1);
    --slow-anim: 200ms ease;
    --fast-anim: 400ms ease-in-out;
}