// ========== modal window  ==========

.modal {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 15;
    transform: translate(-50%, -50%) scale(1);

    padding: 40px;

    width: calc(100vw - 30px);
    max-height: calc(100vh - 30px);

    background: var(--color-background-main);
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 10px;

    overflow-y: auto;
    opacity: 1;
    transition: transform var(--animation), visibility var(--animation),
        opacity var(--animation);

    .backdrop.is-hidden>& {
        transform: translate(-50%, -50%) scale(1.5);
    }

    @include mq(mobile) {
        width: 450px;
    }

    @include mq(tablet) {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) scale(1);
    }

    @include mq(desktop) {
        width: 528px;
        min-height: 581px;
    }
}

.modal__title {
    margin-bottom: 12px;

    font-weight: 700;
    font-size: 20px;
    line-height: 1.15;
    text-align: center;
}

// ========== modal-button ==========

.modal__btn-close {
    position: absolute;
    top: 8px;
    right: 8px;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 30px;
    height: 30px;

    color: var(--color-text-black);
    background: var(--color-background-main);
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 50%;

    cursor: pointer;
    transition: color var(--animation);

    &:hover,
    &:focus {
        color: var(--color-accent);
    }
}

.modal__icon {
    fill: currentColor;
}